
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@for $i from 1 to 15 {
  .div#{$i} {
    width: 800px - (40px*$i);
    height: 800px - (40px * $i);
    background-color:hsl(-40+$i*25,80%,60%) ;
    margin: 20px;
    border-radius: 50%;
    overflow: hidden;
  }
}

.rainbow{
  height: 400px;
  width: 800px;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  margin-top: 200px;
}
.div14{
  background-color: #fff;
}
